import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Button from '../../../components/ui/Button';
import deliverySlice, { getDelivery } from '../../../store/slices/delivery.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {

}

const DeliveryTotal : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('delivery-total');
    const { showSummary } = useSelector(getDelivery);
    const dispatch = useDispatch();

    // HOOKS

    // METHODS
    const toggleSummary = () => {
        dispatch(deliverySlice.actions.toggleSummary());
    }

    return (
        <div className={B()}>
            
            <div className={E('modal', [showSummary ? 'dim' : ''])}>                
                <h1 className={E('title')}>Summary</h1>
                <ul className={E('details')}>
                    <li>
                        <label>Pair 1</label>
                        <p>Progressive vision</p>
                        <span>450 </span>
                    </li>
                    <li>
                        <label>Pair 2</label>
                        <p>Sun Vision</p>
                        <span>450 </span>
                    </li>
                    <li>
                        <label>Total</label>
                        <span>900 </span>
                    </li>
                </ul>

                <div className={E('buttons')}>
                    <Button 
                        text="View more details" 
                        type="secondary" 
                        icon="right-arrow-circle" 
                        click={toggleSummary}/>
                        
                    <Button text="Validate" icon="right-arrow-circle-white"/>
                </div>
            </div>

            <div className={E('dim', [showSummary ? 'on' : ''])}></div>
        </div>
    )
}

export default DeliveryTotal;